import { newE2EPage } from '@stencil/core/testing';

describe('email-overlay', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-email-overlay-ui></presto-email-overlay-ui>');

    const element = await page.find('presto-email-overlay-ui');
    expect(element).toHaveClass('hydrated');
  });
});
